export const TableList = () => {
  const [inputValue, setInputValue] = useState("");
  const [selectedTable, setSelectedTable] = useState(null);

  return (
    <div>
      <h2>Zasadac poriadok</h2>
      <p>Njdi svoje miesto</p>
      <Autocomplete
        disablePortal
        id="TableList-autocomplete"
        options={
          true
            ? tableData.flatMap((table) =>
                table.guests.map((guest) => `${guest} | ${table.name}`)
              )
            : []
        }
        sx={{ margin: "0 4rem 16px 4rem" }}
        renderInput={(params) => (
          <TextField
            {...params}
            placeholder="Meno"
            onClick={() => {
              setInputValue("");
              setSelectedTable(null);
            }}
            InputProps={{
              ...params.InputProps,
              startAdornment: (
                <>
                  <InputAdornment position="start">
                    <Icon>search</Icon>
                  </InputAdornment>
                  {params.InputProps.startAdornment}
                </>
              ),
            }}
          />
        )}
        inputValue={inputValue}
        noOptionsText=""
        onInputChange={(event, newInputValue) => {
          setInputValue(newInputValue);
        }}
        onChange={(event, value) => {
          setSelectedTable(value);
          event.target.blur();
        }}
        size="small"
        freeSolo
      />

      {tableData
        .filter((table) =>
          selectedTable
            ? table.name === selectedTable?.split("|")[1].trim()
            : true
        )
        .map(({ name, guests }) => (
          <div className="TableList-item">
            <h3>{name}</h3>
            <div className="TableList-item-guests">
              {guests.map((guest) => (
                <p
                  className={
                    guest === selectedTable?.split("|")[0].trim() &&
                    "TableList-item-guest-highlight"
                  }
                >
                  {guest}
                </p>
              ))}
            </div>
          </div>
        ))}
    </div>
  );
};
